import React from 'react';
import { Card, CardImg, CardTitle, CardSubtitle, CardText, CardBody } from 'reactstrap';
import { Loading } from './loading';
import { baseUrl } from '../shared/baseUrl';
import { FadeTransform } from 'react-animation-components';


function RenderCard({item, isLoading, errMess}) {
    if(isLoading) {
        return <Loading />;
    } else if(errMess) {
        return <p>{errMess}</p>
    } else 
    return (<FadeTransform
        in
        transformProps={{
            exitTransform: 'scale(0.5) translateY(-50%)'
        }}>
            <Card>
                <CardImg top width='100%' src={baseUrl + item.image} alt={item.name}/>
                <CardBody>
                    <CardTitle>{item.name}</CardTitle>
                    {item.designation? <CardSubtitle>{item.designation}</CardSubtitle> : null}
                    <CardText>{item.description}</CardText>
                </CardBody>
            </Card>
        </FadeTransform>);
}

 const Home = (props) => {
     console.log(props);
    return (
        <div className='container'>
            <div className='row align-items-stretch'>
                <div className='col-12 col-md m-1'><RenderCard item={props.dish} 
                    isLoading={props.dishesLoading} errMess={props.dishesErrMess}
                /></div>
                <div className='col-12 col-md m-1'><RenderCard item={props.promo}
                isLoading={props.promosLoading} errMess={props.promosErrMess}/></div>
                <div className='col-12 col-md m-1'><RenderCard item={props.leader}
                isLoading={props.leadersLoading} errMess={props.leadersErrMess}/></div>
            </div>
        </div>
        
    );
}

export default Home;


